import React, { useEffect } from "react";
import axios from "axios";

export default function ProductsList() {
  useEffect(async () => {
    const url =
      "https://sellka.mysky-shop.pl/api/?function=getProducts&APIkey=cf876a18&start=0&limit=10";
    const res = await fetch(url, {
      Host: "sellka.mysky-shop.pl/api/",
      Origin: "https://korki88.github.io/sellektywni/",
    });
    const data = await res.json();
    console.log(data);
  }, []);

  return (
    <div className="ProductsList">
      <ul className="list">
        <li className="item">
          <img src="./Assets/img/dummy-img.jpg"></img>
          <span>39.99zł</span>
        </li>
      </ul>
    </div>
  );
}
